
import XHeader from 'vux/src/components/x-header/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import VChart from 'vux/src/components/v-chart/v-chart.vue'
import VLine from 'vux/src/components/v-chart/v-line.vue'
import VArea from 'vux/src/components/v-chart/v-area.vue'
import VTooltip from 'vux/src/components/v-chart/v-tooltip.vue'
import VLegend from 'vux/src/components/v-chart/v-legend.vue'
import VBar from 'vux/src/components/v-chart/v-bar.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Query from "@/components/filter";
export default {
data() {
return {
bottonColor:"#3296fa",
title: "新增客户数",
timeFormat:'',
MaxYear:(new Date()).getFullYear(),
thisYear:(new Date()).getFullYear(),
thisMouth:(new Date()).getMonth()+1,
params:{
queryType:1,
year:'',
month:'',
quarter:1,
selUserId:this.$store.state.CurrentUserID,
},
selectMan:'所有成员',
selectStat:'新增客户数',
queryType: 0,
chartData:[],
chartX:[],
chartY:[],
echartDom:{},
listParams:{
queryType:1,
startDate:'',
endDate:''
},
echartsTitle:''
};
},
methods:{

selectTimeFormat(type){
this.$refs.query.changeDateFormat(type)
this.params.quarter = type
this.init()
},

selectedUser(id){

this.params.selUserId = id
this.init();
},

statClick(data){
let type = data.index+1
if(type == 1){this.selectStat = '新增客户数'}
if(type == 2){this.selectStat = '联系跟进次数'}
if(type == 3){this.selectStat = '跟进客户数'}
if(type == 4){this.selectStat = '成交次数'}
if(type == 5){this.selectStat = '成交总额'}
if(type == 6){this.selectStat = '回款总额(按订单成交时间统计)'}
if(type == 7){this.selectStat = '回款总额(按回款时间统计)'}
this.params.queryType = type
this.init()
},

changeDate(flag,date){

if(flag){   //按日统计
this.thisYear = date.split('-')[0]
this.thisMouth = date.split('-')[1]
}else{    //按月统计
this.thisYear = date
}


this.init()
},

init(){
this.params.year = (this.params.quarter==1)? this.MaxYear:this.thisYear
this.params.month = this.thisMouth
this.getAxios("/CustStatis/TimeStatisCustomer", this.params).then(res => {
this.chartX = []
this.chartY = []
res.Data.List.map(item=>{
this.chartX.push(item.Value)
this.chartY.push(item.Key)
this.chartData.push(item)
})
if(this.params.quarter == 2){
this.chartY = this.chartY.map(item=>{
return item.split('-')[1]+'月'
})
}
if(this.params.quarter == 3){
this.chartY = this.chartY.map(item=>{
return item.split('-')[2]+'日'
})
}
this.createChart()
})
},

createChart(){
var _this = this;

let echarts = require('echarts/lib/echarts');
require('echarts/lib/chart/bar');
require('echarts/lib/component/tooltip');
require('echarts/lib/component/legend');
this.echartDom = echarts.init(document.getElementById("myChart"));
this.echartDom.clear()
if(this.params.quarter == 1){
this.echartsTitle = this.chartY[0]+'~'+this.chartY[this.chartY.length-1]+'年'+this.selectStat
}
if(this.params.quarter == 2){
this.echartsTitle = this.thisYear+'年'+this.selectStat
}
if(this.params.quarter == 3){
this.echartsTitle = this.thisYear+'年'+this.thisMouth+'月'+this.selectStat
}
this.echartDom.setOption({
title : {
text: this.echartsTitle,
textStyle:{
fontWeight:'normal',
fontSize:14
},
x:'center',
top:5
},
xAxis : [
{
type: 'value',
name: '',// X坐标抽名字
data : this.chartX,
show: false,
axisTick: {
alignWithLabel: true
},
splitLine: {show: true},//保留网格线
splitArea: {show : false},//去除网格区域
}
],
yAxis : [
{
type: 'category',
name: '日期',
nameLocation: 'start',//y坐标抽名字置顶
inverse: true,
splitLine: {show: true},//保留网格线
splitArea: {show : false},//去除网格区域
data: this.chartY,
triggerEvent: true
}
],

series : [
{
type: 'bar',
data: this.chartX,//蓝色柱形图数据 这里的data数据 经后台接口返回

label: {
normal: {
show: true,
position: 'right'
}
},
itemStyle:{
normal:{ color:'#3398DB' }
}
}
]
})
this.echartDom.off('click')
this.echartDom.on('click', function (params) {
if(params.name){
_this.openList(params.name);
}else{
_this.openList(params.value);
}
})
},

openList(date){

this.listParams.queryType = this.params.queryType;
if(this.params.quarter == 1){       //年
let nextY = parseInt(date)+1
this.listParams.startDate = date +'-1-1 00:00:00'
this.listParams.endDate = date +'-12-31 23:59:59'
}
if(this.params.quarter == 2){       //月
let year = this.thisYear
let mouth = parseInt(date)
let nextM = mouth+1
this.listParams.startDate = year+'-'+mouth+'-1 00:00:00'
let lastDay = (new Date(year,mouth,0)).getDate()
this.listParams.endDate = year+'-'+mouth+'-'+lastDay+' 23:59:59'
}
if(this.params.quarter == 3){       //日
let day = parseInt(date)
let nextD = day+1
this.listParams.startDate = this.thisYear+'-'+this.thisMouth+'-'+day+" 00:00:00"
this.listParams.endDate = this.thisYear+'-'+this.thisMouth+'-'+day+" 23:59:59"
}
let name =
this.listParams.queryType > 3 ? (this.listParams.queryType > 5 ? "汇款" : "订单") : "";
this.$router.push({
path: "/timeStat/statDetail",
query: {
queryType: this.listParams.queryType,
startDate: this.listParams.startDate,
endDate: this.listParams.endDate,
sIndex: 0,
name,
selUserId: this.params.selUserId
}
});
},













selectQueryType(type){
if(this.queryType == type){
this.queryType = 0
}else{
this.queryType = type
}
},
},
mounted(){
this.init()
},
components: {
XHeader,
ButtonTab,
ButtonTabItem,
VChart,
VLine,
VArea,
VTooltip,
VLegend,
VBar,
Datetime,
Group,
XButton,
Query
}
};



